body {
  height: 100%;
  width: 100vw;
  overflow-x: hidden;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth
}
.form {
  height: 100vh;
  width: 100vw;
  display: none;
  align-items: center;
  justify-content: center;
  position: fixed;
  overflow: hidden;
  z-index: 200;
  background-color: #f6f3ef;
}
.form__close-icon {
    height: 50px;
    width: 50px;
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 60px;
    margin-right: 60px;
    cursor: pointer;
}
.form__inner {
    height: 600px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form__link {
  text-decoration: none;
  color: #111111;
}
.form__title {
  font-family: 'Barlow', sans-serif;
  font-size: 48px;
  font-weight: 300;
  line-height: 1.4em;
  letter-spacing: 0.03em;
}
.form__text {
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 200;
  line-height: 1.4em;
  letter-spacing: 0.03em;
  margin-bottom: 26px;
}
.form__inner--flex-justify-center {
    justify-content: center;
}
.form__inner--wider {
    width: 431px;
}
.form__title--margin-bottom {
    margin-bottom: 40px;
}
.form__title--margin-top {
    margin-top: 53px;
}
.form__text--bold {
  font-weight: 600;
}
.input {
    height: 40px;
    width: 300px;
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4em;
    outline: none;
    display: block;
    padding-left: 1em;
    padding-right: 1em;
}
.input__label {
    display: block;
    margin-bottom: 8px;
}
.input--animated {
    background-color: transparent;
    z-index: 10;
    visibility: hidden;
    border: none;
    transition: 1s;
    padding: 0;
}

.active {
    width: 825px;
    transition: 0.8s;
    visibility: visible;
}

.placeholder-hide {
    opacity: 0;
    transition: 0.8s;
}

input::placeholder {
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    color: #514f45;
    opacity: 0.6;
    left: 100px;
}
.input__text {
    font-family: 'Barlow', sans-serif;
    font-size: 14px;
    font-weight: 200;
    line-height: 1.4em;
}
.input--focused:focus {
    border-bottom: 2px solid #797767;
    padding: 3px 3px 2px 12px;
}
.input--extended-margin-bottom {
    margin-bottom: 40px;
}
.input--form {
    height: 42px;
    width: 250px;
    border: none;
    border-bottom: 1px solid #797767;
    padding: 3px 3px 3px 12px;
    margin-bottom: 22px;
    background-color: #f6f3ef;
}
.input--hovered:hover {
    border-bottom: 2px solid #797767;
    padding: 3px 3px 2px 12px;
}
.input--modal {
    width: 74px;
    color: #575757;
    border: 1px solid #c0c0c0;
    margin-bottom: 50px;
}
.button {
    height: 42px;
    width: 200px;
    padding: 8px 16px;
    cursor: pointer;
    background-color: #797767;
    border: 1px #797767 solid;
}
.button__icon {
    transition: transform 0.4s ease;
    pointer-events: none;
}

.icon-rotate {
    transform: rotate(180deg);
}
.button__text {
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    line-height: 1.4em;
    font-weight: 200;
    pointer-events: none;
    color: #FFFFFF;
}
.button--form {
    width: 250px;
    margin-bottom: 16px;
}
.button--hovered:hover {
    opacity: 0.6;
    transition: 0.5s ease;
}
.button--dropdown {
    min-width: 200px;
    width: fit-content;
    display: flex;
    justify-content: space-between;
    background: transparent;
    text-overflow: ellipsis;
    white-space: nowrap;
    border: 1px solid rgba(110, 119, 93, 0.4);
    margin: 25px 0 20px 0;
    padding: 8px 0;
}
.button--large {
    width: 352px;
}
.button--swap {
    width: 242px;
    position: relative;
    bottom: 70px;
    left: 122px;
    opacity: 0;
    transition: opacity 0.2s ease;
    background-color: transparent;
}
.button__icon--margin-y {
    margin: 0 8px;
}
.button__text--hovered:hover {
    opacity: 0.6;
}
.button__text--black {
    color: #111111;
}
.button__text--margin-y {
    margin: 0 8px;
}
.social-media-login {
    height: 30px;
    width: 180px;
    display: flex;
    justify-content: space-evenly;
    cursor: pointer;
}
.social-media-login__image {
    height: 28px;
    width: 28px;
}
.navigation {
    height: 165px;
    width: 100vw;
    display: inline-flex;
}
.navigation__inner {
    height: 343px;
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: #FFFFFF;
}
.navigation__link {
    text-decoration: none;
}
.navigation__item {
    min-width: 130px;
}
.navigation__menu {
    display: flex;
    justify-content: center;
    align-items: center;
}
.navigation__list {
    align-items: center;
    display: inline-flex;
    list-style: none;
    text-align: center;
}
.navigation__text {
    font-family: Barlow,sans-serif;
    font-size: 16px;
    font-weight: 300;
    color: #514f45;
}
.navigation--cream {
    background-color: #f6f3ef;
}
.navigation--flex-align-end {
    align-items: flex-end;
}
.navigation--higher {
    height: 365px;
}
.navigation--flex-justify-center {
    justify-content: center;
}
.navigation__item--hovered:hover {
    opacity: 0.6;
}
.navigation__item--icon {
    height: 20px;
    width: 20px;
    font-family: 'Barlow', sans-serif;
    font-size: 65px;
    cursor: pointer;
}
.navigation__item--wider {
    min-width: 200px;
}
.navigation__item--side {
    min-width: 65px;
}
.navgation__menu--footer {
    width: 25vw;
}
.navigation__menu--relative {
    position: relative;
}
.navigation__menu--side {
    height: 80px;
    width: 230px;
    position: absolute;
    right: 0;
}
.navigation__list--text-align-end {
    text-align: end !important;
}
.navigation__list--vertical {
    height: 250px;
    gap: 30px;
    text-align: start;
    flex-direction: column;
    justify-content: flex-start;
}
.navigation__text--hovered:hover {
    opacity: 0.6;
}
.logo {
    height: 160px;
    width: 160px;
    cursor: pointer;
}
.logo__image {
  height: 160px;
  width: 160px;
}
.logo__link {
    text-decoration: none;
}
.logo--big {
    height: 250px;
    width: 500px;
}
.logo--footer {
    position: absolute;
    left: -210px;
    bottom: 180px;
}
.logo__image--small {
    height: 123px;
    width: 123px;
}
.header {
    height: 130px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header__line {
    width: 25px;
    border: 1.5px #514f45 solid;
}
.header__link {
    text-decoration: none;
    color: #111111;
}
.header__title {
    font-family: 'Poppins', sans-serif;
    font-size: 42px;
    line-height: 1.1em;
    letter-spacing: 0.2em;
}
.header__text {
    font-family: 'work sans' ,sans-serif;
    font-size: 20px;
    font-weight: 200;
    line-height: 1.4em;
}
.header--flex-direction-column {
    flex-direction: column;
}
.header--small {
    height: 100px;
}
.header__title--section {
    font-size: 32px;
    letter-spacing: 0.275em;
    font-weight: 400;
    margin-bottom: 38px;
    margin-top: 90px;
    color: #514f45;
}
.tab {
    height: fit-content;
    width: 100vw;
    background-color: #f6f3ef;
    display: flex;
    justify-content: center;
}
.tab__card {
    width: 320px;
    text-align: center;
}
.tab__inner {
    display: flex;
}
.tab__link {
    text-decoration: none;
}
.tab__title {
    font-family: 'Poppins', sans-serif;
    font-size: 28px;
    font-weight: 200;
    line-height: 2em;
    letter-spacing: -0.02em;
    color: #514f45;
}
.tab__text {
    font-family: 'Barlow', sans-serif;
    font-size: 16px;
    font-weight: 200;
    line-height: 1.7em;
    letter-spacing: 0.01em;
    color: #111111;
}
.tab--flex-dir-column {
    flex-direction: column;
}
.tab--white {
    background-color: #FFFFFF;
}
.tab__card--small {
    height: 100px;
}
.tab__inner--flex-align-center {
    align-items: center;
}
.tab__inner--flex-align-items-end {
    align-items: flex-end;
}
.tab__inner--flex-dir-column {
    flex-direction: column;
}
.tab__inner--flex-justify-center {
    justify-content: center;
}
.tab__inner--medium {
    height: 330px;
}
.tab__inner--width-1000 {
    width: 1000px;
}
.tab__inner--width-1250 {
    width: 1250px;
}
.tab__inner--high {
    min-height: 470px;
}
.social-media-icons {
    width: 120px;
    display: inline-flex;
    justify-content: center;
    position: fixed;
    left: 0;
    bottom: 10px;
    z-index: 15;
}
.social-media-icons__image {
    height: 25px;
    width: 25px;
    margin: 0 10px;
}